<template>
  <b-container>
    <b-row>
      <b-col md="6" offset="3" class="mt-4 content">
        <form @submit="handleSubmit($event)">
          <h2 class="title">Delete Site</h2>
          <hr />
          <AlertComponent v-if="showAlert" v-bind="alertInfo"></AlertComponent>
          <div class="form-group">
            <label>Site Name</label>
            <input v-model="model.siteName" type="text" class="form-control" disabled />
          </div>
          <b-row>
            <b-col md="12">
              <p class="float-left">
                <router-link to="/sites" class="link">Back to List</router-link>
              </p>
              <div class="form-group float-right">
                <button type="submit" class="btn">
                <span class="mr-2">Delete</span>
                <font-awesome-icon icon="trash-alt" class="icons" />
              </button>
              </div>
            </b-col>
          </b-row>
        </form>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import AlertComponent from '../../../shared/alert/alert.component'
import SiteService from '../site.service'
import router from '../../../router'

export default {
  name: 'DeleteSiteComponent',
  components: {
    AlertComponent,
  },
  data() {
    return {
      showAlert: false,
      alertInfo: null,
      model: {
        siteName: '',
        isDeleted: true,
      },
    }
  },
  created() {
    let vm = this
    SiteService.getSite(this.$route.params.id)
      .then((response) => {
        vm.model = response.data
      })
      .catch(function(error) {
        vm.showAlert = true
        vm.alertInfo = { success: false, error }
        return
      })
  },
  methods: {
    handleSubmit(event) {
      event.preventDefault()
      this.showAlert = false
      let vm = this
      SiteService.deleteSite(this.$route.params.id)
        .then(() => {
          vm.showAlert = true
		  vm.alertInfo = { success: true }
		  router.push({path: '/sites'})
        })
        .catch(function(error) {
          vm.showAlert = true
          vm.alertInfo = { success: false, error }
        })
    },
  },
}
</script>

<style scoped>

  .link {
    text-decoration: none;
    color: #787777;
  }

  .content {
    background-color: white;
    box-shadow: 0 0 10px #ccc;
    padding: 50px 30px 20px 30px;
  }

  input {
    border-radius: 0px;
  }

  .title {
    font-family: 'apercu-medium';
  }
</style>
