import VueRouter from 'vue-router'
import AuthService from './core/auth.service'
import HomeComponent from './components/home/home.component'
import ContactComponent from './components/contact/contact.component'
import AboutComponent from './components/about/about.component'
import LoginComponent from './shared/login/login.component'
import RegisterComponent from './shared/register/register.component'
import NotFoundComponent from './shared/notfound/notfound.component'
import ListContactComponent from './components/contact/list-contact/list.contact.component'
import AddContactComponent from './components/contact/add-contact/add.contact.component'
import EditContactComponent from './components/contact/edit-contact/edit.contact.component'
import DeleteContactComponent from './components/contact/delete-contact/delete.contact.component'
import ListSiteComponent from './components/site/list-site/list.site.component'
import AddSiteComponent from './components/site/add-site/add.site.component'
import EditSiteComponent from './components/site/edit-site/edit.site.component'
import DeleteSiteComponent from './components/site/delete-site/delete.site.component'
import ListCompanyComponent from './components/company/list-company/list.company.component'
import AddCompanyComponent from './components/company/add-company/add.company.component'
import EditCompanyComponent from './components/company/edit-company/edit.company.component'
import DeleteCompanyComponent from './components/company/delete-company/delete.company.component'


const routes = [
  {
    path: '/',
    component: HomeComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/contact',
    component: ContactComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/about',
    component: AboutComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/login',
    component: LoginComponent,
  },
  {
    path: '/register',
    component: RegisterComponent,
  },
	{
    path: '/contacts',
    component: ListContactComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/contact/add',
    component: AddContactComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/contact/edit/:id',
    component: EditContactComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/contact/delete/:id',
    component: DeleteContactComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/sites',
    component: ListSiteComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/site/add',
    component: AddSiteComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/site/edit/:id',
    component: EditSiteComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/site/delete/:id',
    component: DeleteSiteComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/companies',
    component: ListCompanyComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/company/add',
    component: AddCompanyComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/company/edit/:id',
    component: EditCompanyComponent,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: '/company/delete/:id',
    component: DeleteCompanyComponent,
    meta: {
      requiresAuth: true,
    },
  },
  
  {
    path: '*',
    component: NotFoundComponent,
  },
]

const router = new VueRouter({ routes, mode: 'history' })

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!AuthService.isAuthenticated()) {
      return next('/login')
    }
  }

  next()
})

export default router
