<template>
  <b-container>
    <form @submit="handleSubmit($event, '/contacts')" class="form">
      <b-row>
        <b-col md="12" class="mt-4">
          <div class="header-contact">
            <h2>Add Contact</h2>
            <hr />
            <AlertComponent v-if="showAlert" v-bind="alertInfo"></AlertComponent>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col md="6">
          <div class="form-group">
            <label>First Name</label>
            <input v-model="model.firstName" type="text" class="form-control"  required maxlength="50" />
          </div>
          <div class="form-group">
            <label>Last Name</label>
            <input v-model="model.lastName" type="text" class="form-control"  required maxlength="50" />
          </div>
          <div class="form-group">
            <label>Middle Initial</label>
            <input v-model="model.middleInitial" type="text" class="form-control"  required maxlength="2" />
          </div>
        </b-col>
        <b-col md="6">
          <div class="form-group">
            <label>Email</label>
            <input v-model="model.email" type="email" class="form-control" required maxlength="256" />
          </div>
          <div class="form-group">
            <label>Tel No</label>
            <input v-model="model.telNo" type="text" class="form-control"  required maxlength="25" />
          </div>
          <div class="form-group">
            <label>Site</label>
            <select v-model="model.siteId" class="form-control">
              <option disabled="" value="">Select site</option>
              <option v-for="site in sites" :value="site.siteId" :key="site.siteId">{{
                site.siteName
              }}</option>
            </select>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col md="12">
          <div class="form-group float-right">
            <button type="submit" class="btn">
              <span class="mr-3">Submit</span>
              <font-awesome-icon icon="paper-plane" class="icons" />
            </button>
          </div>
        </b-col>
      </b-row>
    </form>
      <b-row>
        <p class="ml-3">
          <router-link to="/contacts" class="link" id="contact-back-to-list">Back to List</router-link>
        </p>
      </b-row>
  </b-container>
</template>

<script>
import AlertComponent from '../../../shared/alert/alert.component'
import ContactService from '../contact.service'
import SiteService from '../../site/site.service'
import router from '../../../router'

export default {
  name: 'AddContactComponent',
  components: {
    AlertComponent,
  },
  data() {
    return {
      showAlert: false,
      alertInfo: null,
      model: {
        firstName: '',
        lastName: '',
        middleInitial: '',
        email: '',
        telNo: '',
        isDeleted: false,
        siteId: '',
      },
      sites: null,
    }
  },

  created() {
    let vm = this
    SiteService.getAllSites()
      .then((response) => {
        vm.sites = response.data
      })
      .catch(function(error) {
        vm.showAlert = true
        vm.alertInfo = { success: false, error }
      })
  },

  methods: {
    handleSubmit(event, path) {
      event.preventDefault()
      this.showAlert = false
      let vm = this
      ContactService.addContact(this.model)
        .then(() => {
          vm.showAlert = true
          vm.alertInfo = { success: true }
          router.push({path: path})
        })
        .catch(function(error) {
          vm.showAlert = true
          vm.alertInfo = { success: false, error }
        })
    },
  },
}
</script>

<style scoped>
  .link {
    text-decoration: none;
  }
</style>
