import axios from 'axios'
import AuthService from '../../core/auth.service'

class ContactService {
	token = AuthService.getToken()

	addContact = contact => {
		const url = process.env.VUE_APP_HOST_URL + '/contact'
		return axios.post(url, contact, { headers: { Authorization: `Bearer ${this.token}` } })
	}

	editContact = (id, contact) => {
		const url = process.env.VUE_APP_HOST_URL + '/contact/' + id
		return axios.put(url, contact, { headers: { Authorization: `Bearer ${this.token}` } })
	}

	deleteContact = id => {
		const url = process.env.VUE_APP_HOST_URL + '/contact/' + id
		return axios.delete(url, { headers: { Authorization: `Bearer ${this.token}` } })
	}

	getContact = id => {
		const url = process.env.VUE_APP_HOST_URL + '/contact/' + id
		return axios.get(url, { headers: { Authorization: `Bearer ${this.token}` } })
	}

	getAllContacts = () => {
		const url = process.env.VUE_APP_HOST_URL + '/contact'
		return axios.get(url, { headers: { Authorization: `Bearer ${this.token}` } })
	}
}

const contactService = new ContactService()
export default contactService