<template>
  <div id="app" class="page-container">
	  <div class="content">
      <NavbarComponent v-bind="{ isAuthenticated }" @EventLoggedOut="EventLoggedOut"></NavbarComponent>
      <div class="container">
        <router-view class="mb-5" @EventLoggedIn="EventLoggedIn" />
      </div>
    </div>
    <div class="content-footer">
      <FooterComponent v-bind="{ isAuthenticated }" @EventLoggedOut="EventLoggedOut"/>
    </div>
  </div>
</template>

<script>
import AuthService from './core/auth.service'
import NavbarComponent from './shared/navbar/navbar.component'
import FooterComponent from './shared/footer/footer.component'
import router from './router'

export default {
  name: 'app',
  data() {
    return {
      isAuthenticated: {
        status: false,
      },
    }
  },
  created() {
    this.isAuthenticated.status = AuthService.isAuthenticated()
  },
  methods: {
    EventLoggedIn() {
      this.isAuthenticated.status = true
      router.push({ path: '/' })
    },
    EventLoggedOut() {
      this.isAuthenticated.status = false
      router.push({ path: '/login' })
    }
  },
  components: {
    NavbarComponent,
    FooterComponent,
  },
}
</script>

<style>
  body{
    background-color: #f8f8f8;
    font-family: apercu-light;
    color: #425b71;;
  }

  @font-face {
    font-family: 'apercu-light';
    src: url('./assets/fonts/Apercu-Light.otf');
  }

  @font-face {
    font-family: 'apercu-medium';
    src: url('./assets/fonts/Apercu-Medium.otf');
  }
  
  @font-face {
    font-family: 'apercu-bold';
    src: url('./assets/fonts/Apercu-Bold.otf');
  }

  .page-container {
    position: relative;
    min-height: 100vh;
  }

  .content {
    padding-bottom: 2.5rem;
  }

  .content-footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2.5rem;
  }
</style>
