<template>
  <b-container class="content">
    <AlertComponent v-if="showAlert" v-bind="errorResponse"></AlertComponent>
    <b-row class="row mb-4">
      <b-col md="12" class="mt-4">
        <h2 class="float-left title">Sites</h2>
        <span class="float-right">
          <button class="btn mr-2 btn-success" @click="importCsv">
            <span class="mr-2">Import Csv</span>
            <font-awesome-icon icon="file-import" />
          </button>
          <router-link to="/site/add">
            <button class="btn btn-primary">
              <span class="mr-2">Add Site</span>
              <font-awesome-icon icon="plus" />
            </button>
          </router-link>
        </span>
      </b-col>
    </b-row>
    <b-row >
      <b-col md="12">
        <table class="table table-striped table-light table-hover">
        <thead>
          <tr>
            <th scope="col">Site Name</th>
            <th scope="col">Address</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="site in sites" :key="site.siteId">
            <td>{{ site.siteName }}</td>
            <td>{{ site.address }}</td>
            <td>
              <span class="float-right">
                <router-link :to="`/site/edit/${site.siteId}`"><font-awesome-icon icon="edit" class="edit-icons" /></router-link> |&nbsp;
                <router-link :to="`/site/delete/${site.siteId}`"><font-awesome-icon icon="trash-alt" class="delete-icons" /></router-link>
              </span>
            </td>
          </tr>
        </tbody>
      </table>
      </b-col>
    </b-row>
    <router-link to="/" class="link">Back to Home</router-link>
  </b-container>
</template>

<script>
import AlertComponent from '../../../shared/alert/alert.component'
import SiteService from '../site.service'

export default {
  name: 'ListSitesComponent',
  components: {
    AlertComponent,
  },
  data() {
    return {
      sites: null,
      showAlert: null,
      errorResponse: { error: null },
    }
  },
  created() {
    this.showAlert = false
    let vm = this
    SiteService.getAllSites()
      .then((response) => {
        vm.sites = response.data
        console.log(vm.sites);
      })
      .catch(function(error) {
        vm.showAlert = true
        vm.errorResponse.error = error
      })
  },
  methods: {
    importCsv() {
      alert('Not yet implemented')
    }
  }
}
</script>

<style scoped>

  .title {
    font-family: 'apercu-medium';
  }

  .edit-icons {
    color: #4227ca;
  }

  .edit-icons:hover {
    color: #425b71;
  }

  .delete-icons {
    color: #e34444;
  }

  .delete-icons:hover {
    color: #c23636;
  }

  .table {
    color: #425b71;
    box-shadow: 0 0 2px #425b71;
  }

  .link {
    text-decoration: none;
    color: #787777;
  }

  .content {
    background-color: white;
    box-shadow: 0 0 10px #ccc;
  }
</style>
