<template>
  <div class="container">
    <b-row class="row mb-4">
      <b-col md="12" class="mt-4">
        <h2 class="float-left">Contacts</h2>
        <AlertComponent v-if="showAlert" v-bind="errorResponse"></AlertComponent>
        <span class="float-right">
          <router-link to="/contact/add">
            <button class="btn">
              <span class="mr-2">Add New Contact</span>
              <font-awesome-icon icon="plus-circle" />
            </button>
          </router-link>
        </span>
      </b-col>
    </b-row>
    <b-row style="overflow-x:auto;">
      <table class="table table-condensed">
        <thead>
          <tr>
            <th scope="col">First Name</th>
            <th scope="col">Last Name</th>
            <th scope="col">Middle Initial</th>
            <th scope="col">Email</th>
            <th scope="col">Tel No</th>
            <th scope="col">Site</th>

            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="contact in contacts" :key="contact.contactId">
            <td>{{ contact.firstName }}</td>
            <td>{{ contact.lastName }}</td>
            <td>{{ contact.middleInitial }}</td>
            <td>{{ contact.email }}</td>
            <td>{{ contact.telNo }}</td>

            <td>
              <span class="float-right">
                <router-link :to="`/contact/edit/${contact.contactId}`"><font-awesome-icon icon="edit" class="edit-icons" /></router-link> |&nbsp;
                <router-link :to="`/contact/delete/${contact.contactId}`"><font-awesome-icon icon="trash-alt" class="delete-icons" /></router-link>
              </span>
            </td>
          </tr>
        </tbody>
      </table>
    </b-row>
    <router-link to="/" class="link">Back to Home</router-link>
  </div>
</template>

<script>
import AlertComponent from '../../../shared/alert/alert.component'
import ContactService from '../contact.service'

export default {
  name: 'ListContactsComponent',
  components: {
    AlertComponent,
  },
  data() {
    return {
      contacts: null,
      showAlert: null,
      errorResponse: { error: null },
    }
  },
  created() {
    this.showAlert = false
    let vm = this
    ContactService.getAllContacts()
      .then((response) => {
        vm.contacts = response.data
        console.log(vm.contacts)
      })
      .catch(function(error) {
        vm.showAlert = true
        vm.errorResponse.error = error
      })
  }
}
</script>

<style scoped>

  .edit-icons {
    color: #40b883;
  }

  .edit-icons:hover {
    color: #36966c;
  }

  .delete-icons {
    color: #e84c3d;
  }

  .delete-icons:hover {
    color: #ca302d;
  }

  .table {
    color: #425b71;
  }

  .link {
    text-decoration: none;
  }
</style>
