<template>
  <b-container class="content">
    <b-row>
      <b-col md="12" class="mt-4">
        <h2 class="title">Edit Contact</h2>
      </b-col>
    </b-row>
    <AlertComponent v-if="showAlert" v-bind="alertInfo"></AlertComponent>
    <hr />
    <form @submit="handleSubmit($event)">
      <b-row>
        <b-col md="6">
          <div class="form-group">
            <label>First Name</label>
            <input
              v-model="model.firstName"
              type="text"
              class="form-control"
              required
              maxlength="50"
            />
          </div>
          <div class="form-group">
            <label>Last Name</label>
            <input
              v-model="model.lastName"
              type="text"
              class="form-control"
              required
              maxlength="50"
            />
          </div>
          <div class="form-group">
            <label>Middle Initial</label>
            <input
              v-model="model.middleInitial"
              type="text"
              class="form-control"
              required
              maxlength="2"
            />
          </div>
        </b-col>
        <b-col md="6">
          <div class="form-group">
            <label>Email</label>
            <input
              v-model="model.email"
              type="text"
              class="form-control"
              required
              maxlength="256"
            />
          </div>
          <div class="form-group">
            <label>Tel No</label>
            <input v-model="model.telNo" type="text" class="form-control" required maxlength="25" />
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col md="12">
          <p class="float-left">
            <router-link :to="`/sites`" class="link">Back to site</router-link>
          </p>
          <div class="form-group float-right">
            <button type="submit" class="btn">
              <span class="mr-3">Submit</span>
              <font-awesome-icon icon="paper-plane" class="icons" />
            </button>
          </div>
        </b-col>
      </b-row>
    </form>
  </b-container>
</template>

<script>
import AlertComponent from '../../../shared/alert/alert.component'
import ContactService from '../contact.service'
import SiteService from '../../site/site.service'
import router from '../../../router'

export default {
  name: 'EditContactComponent',
  components: {
    AlertComponent,
  },
  data() {
    return {
      showAlert: false,
      alertInfo: null,
      model: {
        firstName: '',
        lastName: '',
        middleInitial: '',
        email: '',
        telNo: '',
        isDeleted: true,
        siteId: '',
      },
      sites: null,
    }
  },
  created() {
    let vm = this
    ContactService.getContact(this.$route.params.id)
      .then((response) => {
        vm.model = response.data
      })
      .catch(function(error) {
        vm.showAlert = true
        vm.alertInfo = { success: false, error }
        return
      })
    SiteService.getAllSites()
      .then((response) => {
        vm.sites = response.data
      })
      .catch(function(error) {
        vm.showAlert = true
        vm.alertInfo = { success: false, error }
      })
  },
  methods: {
    handleSubmit(event) {
      event.preventDefault()
      this.showAlert = false
      let vm = this
      ContactService.editContact(this.$route.params.id, this.model)
        .then(() => {
          vm.showAlert = true
          vm.alertInfo = { success: true }
          router.push({ path: `/sites` })
        })
        .catch(function(error) {
          vm.showAlert = true
          vm.alertInfo = { success: false, error }
        })
    },
  },
}
</script>

<style scoped>
.link {
  text-decoration: none;
}

.form-control:focus {
  border: 2px solid #9b9d9e;
  -webkit-box-shadow: none;
  box-shadow: none;
}

input {
  border-radius: 0px;
}

.form-control:focus {
  border: 1px solid #4227ca;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.content {
  background-color: white;
  box-shadow: 0 0 10px #ccc;
}

.link {
  text-decoration: none;
  color: #787777;
}

.title {
  font-family: 'apercu-medium';
}
</style>
