<template>
  <b-container class="content">
    <b-row>
      <b-col md="12" class="mt-4 mb-3">
        <h2 class="float-left title">Add Site</h2>
      </b-col>
    </b-row>
    <AlertComponent v-if="showAlert" v-bind="alertInfo"></AlertComponent>
    <form @submit="handleSubmit($event)">
    <b-row>
      <b-col md="6">
          <div class="form-group">
            <label>Site Name</label>
            <input
              v-model="model.siteName"
              type="text"
              class="form-control"
              required
            />
          </div>
      </b-col>
      <b-col md="6">
          <div class="form-group">
            <label>Address</label>
            <input
              v-model="model.address"
              type="text"
              class="form-control"
              required
            />
          </div>
      </b-col>
    </b-row>

    
      <b-row>
      <b-col md="12">
          <div class="form-group float-right">
          <b-button variant="primary" v-b-modal.modal-1 class="float-right">
            <span class="mr-2">Add Contact</span>
            <font-awesome-icon icon="plus" />
          </b-button>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col md="12" style="overflow-x:auto;">
        <div class="mb-0 pl-3">
          <table class="table table-striped table-light table-hover mt-4">
          <thead>
            <tr>
              <th scope="col">First Name</th>
              <th scope="col">Last Name</th>
              <th scope="col">Middle Initial</th>
              <th scope="col">Email</th>
              <th scope="col">Tel No</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-bind:key="contact.id" v-for="contact in model.contacts">
              <td>{{ contact.firstName }}</td>
              <td>{{ contact.lastName }}</td>
              <td>{{ contact.middleInitial }}</td>
              <td>{{ contact.email }}</td>
              <td>{{ contact.telNo }}</td>
              <td>
                <span class="float-right">
                  <font-awesome-icon icon="trash-alt" class="delete-icons" @click="removeRow(contact.key)" />
                </span>
              </td>
            </tr>
          </tbody>
        </table>
        </div>
      </b-col>
      </b-row>
      <b-row>
          <b-col md="12">
        <div class="form-group float-right">
          <button type="submit" class="btn float-right btn-info">
            <span>Save</span>
            <font-awesome-icon icon="save" class="icons" />
          </button>
          <router-link to="/sites" class="text">
            <button type="button" class="btn btn-light mr-2">
              Cancel
            </button>
          </router-link>
        </div>
      </b-col>
        </b-row>
    </form>

    <b-modal id="modal-1" title="Add Contact" class="modal" ref="modal" @ok="handleOk" @show="resetModal" @hidden="resetModal" 
    cancel-variant="default"
    ok-variant="default">
      <form class="form" ref="form" @submit.stop.prevent="handleModalSubmit">
        <b-row>
          <b-col md="6">
          <b-form-group :state="contactModel.firstNameState" label="First name" label-for="first-name" invalid-feedback="First name is required">
            <b-form-input id="first-name" v-model="contactModel.firstName" :state="contactModel.firstNameState" max="50" required></b-form-input>
          </b-form-group>
          <b-form-group :state="contactModel.lastNameState" label="Last name" label-for="last-name" invalid-feedback="Last name is required">
            <b-form-input id="last-name" v-model="contactModel.lastName" :state="contactModel.lastNameState" max="50" required></b-form-input>
          </b-form-group>
          <b-form-group :state="contactModel.middleInitialState" label="Middle initial" label-for="middle-initial" invalid-feedback="Middle initial is required">
            <b-form-input id="middle-initial" v-model="contactModel.middleInitial" :state="contactModel.middleInitialState" max="2" required></b-form-input>
          </b-form-group>
        </b-col>
        <b-col md="6">
          <b-form-group :state="contactModel.emailState" label="Email" label-for="email" invalid-feedback="Email is required or the value is invalid">
            <b-form-input id="email" v-model="contactModel.email" :state="contactModel.emailState" type="email" max="256" required></b-form-input>
          </b-form-group>
          <b-form-group :state="contactModel.telNoState" label="Tel No" label-for="tel-no" invalid-feedback="Tel No is required">
            <b-form-input id="tel-no" v-model="contactModel.telNo" :state="contactModel.telNoState" max="25" required></b-form-input>
          </b-form-group>
        </b-col>
        </b-row>
      </form>
    </b-modal>
  </b-container>
</template>

<script>
import AlertComponent from '../../../shared/alert/alert.component'

import SiteService from '../site.service'
import ContactService from '../../contact/contact.service'
import router from '../../../router'

export default {
  name: 'AddSiteComponent',
  components: {
    AlertComponent
  },
  data() {
    return {
      showAlert: false,
      alertInfo: null,
      model: {
        siteName: '',
        address: '',
        contacts: [],
      },
      contactModel: {
        firstName: '',
        firstNameState: null,
        lastName: '',
        lastNameState: null,
        middleInitial: '',
        middleInitialState: null,
        email: '',
        emailState: null,
        telNo: '',
        telNoState: null,
        key: null
      }
    }
  },

  methods: {
    handleSubmit(event) {
      event.preventDefault()
      this.showAlert = false
      let vm = this
      SiteService.addSite(this.model)
        .then(() => {
          vm.showAlert = true
          vm.alertInfo = { success: true }
          router.push({ path: '/sites' })
        })
        .catch(function(error) {
          vm.showAlert = true
          vm.alertInfo = { success: false, error }
      })
    },
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity()
      this.contactModel.firstNameState = valid
      this.contactModel.lastNameState = valid
      this.contactModel.middleInitialState = valid
      this.contactModel.emailState = valid
      this.contactModel.telNoState = valid
      return valid
    },
    resetModal() {
      this.contactModel.firstName = ''
      this.contactModel.firstNameState = null
      this.contactModel.lastName = ''
      this.contactModel.lastNameState = null
      this.contactModel.middleInitial = ''
      this.contactModel.middleInitialState = null
      this.contactModel.email = ''
      this.contactModel.emailState = null
      this.contactModel.telNo = ''
      this.contactModel.telNoState = null
    },
    handleOk(bvModalEvt) {
      bvModalEvt.preventDefault()
      
      if (!this.checkFormValidity()) {
        return
      }
      
      this.model.contacts.push({
        firstName: this.contactModel.firstName,
        lastName: this.contactModel.lastName,
        middleInitial: this.contactModel.middleInitial,
        email: this.contactModel.email,
        telNo: this.contactModel.telNo,
        key: this.uuidv4()
      })
      this.$nextTick(() => {
        this.$bvModal.hide('modal-1')
      })
    },
    removeRow(key) {
      var index = this.model.contacts.findIndex(item => item.key === key);
      //var index = this.model.contacts.indexOf(key)
      this.model.contacts.splice(index, 1)
    },
    uuidv4() {
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
      });
    }
  },
}
</script>

<style scoped>

  */deep/ .modal > .modal-dialog > .modal-content > .modal-header {
    background-color: #4227ca;
    color: white;
  }

  */deep/ .modal > .modal-dialog > .modal-content > .modal-header > .close {
    color: white;
  }

  */deep/ .modal > .modal-dialog > .modal-content > .modal-body {
    padding-top: 50px;
    padding-bottom: 50px;
    padding-right: 30px;
    padding-left: 30px;
  }

  */deep/ .modal > .modal-dialog > .modal-content > .modal-body > .form > .row > .col-md-6 > .form-group > .d-block {
    color: #428bca;
    font-family: 'apercu-medium';
  }

  .link {
    text-decoration: none;
    color: #787777;
  }

  .form-control:focus {
    border: 1px solid #4227ca;
    -webkit-box-shadow: none;
    box-shadow: none;
  }

  .delete-icons {
    color: #e34444;
    cursor: pointer;
  }

  .delete-icons:hover {
    color: #c23636;
  }

  .content {
    background-color: white;
    box-shadow: 0 0 10px #ccc;
  }

  input {
    border-radius: 0px;
  }

  .title {
    font-family: 'apercu-medium';
  }

  .table {
    color: #425b71;
    box-shadow: 0 0 2px #425b71;
  }
</style>
