import axios from 'axios'
import AuthService from '../../core/auth.service'

class SiteService {
	token = AuthService.getToken()

	addSite = site => {
		const url = process.env.VUE_APP_HOST_URL + '/site'
		return axios.post(url, site, { headers: { Authorization: `Bearer ${this.token}` } })
	}

	editSite = (id, site) => {
		const url = process.env.VUE_APP_HOST_URL + '/site/' + id
		return axios.put(url, site, { headers: { Authorization: `Bearer ${this.token}` } })
	}

	deleteSite = id => {
		const url = process.env.VUE_APP_HOST_URL + '/site/' + id
		return axios.delete(url, { headers: { Authorization: `Bearer ${this.token}` } })
	}

	getSite = id => {
		const url = process.env.VUE_APP_HOST_URL + '/site/' + id
		return axios.get(url, { headers: { Authorization: `Bearer ${this.token}` } })
	}

	getAllSites = () => {
		const url = process.env.VUE_APP_HOST_URL + '/site'
		return axios.get(url, { headers: { Authorization: `Bearer ${this.token}` } })
	}
}

const siteService = new SiteService()
export default siteService