<template>
  <b-container class="body-content">
    <b-row>
      <b-col md="12" class="mt-4">
        <form @submit="handleSubmit($event)">
          <h2 class="content">Delete Contact</h2>
          <AlertComponent v-if="showAlert" v-bind="alertInfo"></AlertComponent>
          <b-row>
            <b-col md="6">
              <div class="form-group">
                <label>First Name</label>
                <input v-model="model.firstName" type="text" class="form-control" disabled />
              </div>
              <div class="form-group">
                <label>Last Name</label>
                <input v-model="model.lastName" type="text" class="form-control" disabled />
              </div>
              <div class="form-group">
                <label>Middle Initial</label>
                <input v-model="model.middleInitial" type="text" class="form-control" disabled />
              </div>
            </b-col>
            <b-col md="6">
              <div class="form-group">
                <label>Email</label>
                <input v-model="model.email" type="text" class="form-control" disabled />
              </div>
              <div class="form-group">
                <label>Tel No</label>
                <input v-model="model.telNo" type="text" class="form-control" disabled />
              </div>
              <div class="form-group" hidden>
                <label>Site</label>
                <input v-model="model.siteId" type="text" class="form-control" />
              </div>
            </b-col>
          </b-row>
          <b-row>
            <b-col md="12">
              <p class="float-left">
                <router-link to="/sites" class="link">Back to Site</router-link>
              </p>
              <div class="form-group float-right">
                <button type="submit" class="btn">
                  <span class="mr-2">Delete</span>
                  <font-awesome-icon icon="trash-alt" class="icons" />
                </button>
              </div>
            </b-col>
          </b-row>
        </form>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import AlertComponent from '../../../shared/alert/alert.component'
import ContactService from '../contact.service'
import SiteService from '../../site/site.service'
import router from '../../../router'

export default {
  name: 'DeleteContactComponent',
  components: {
    AlertComponent,
  },
  data() {
    return {
      showAlert: false,
      alertInfo: null,
      model: {
        firstName: '',
        lastName: '',
        middleInitial: '',
        email: '',
        telNo: '',
        isDeleted: true,
        siteId: '',
      },
      sites: null,
    }
  },
  created() {
    let vm = this
    ContactService.getContact(this.$route.params.id)
      .then((response) => {
        vm.model = response.data
      })
      .catch(function(error) {
        vm.showAlert = true
        vm.alertInfo = { success: false, error }
        return
      })
  },
  methods: {
    handleSubmit(event) {
      event.preventDefault()
      this.showAlert = false
      let vm = this
      this.siteId = null
      ContactService.editContact(this.$route.params.id, this.model)
        .then(() => {
          vm.showAlert = true
          vm.alertInfo = { success: true }
          router.push({ path: '/sites' })
        })
        .catch(function(error) {
          vm.showAlert = true
          vm.alertInfo = { success: false, error }
        })
    },
  },
}
</script>

<style scoped>

.link {
  text-decoration: none;
  color: #787777;
}

.body-content {
  background-color: white;
  box-shadow: 0 0 10px #ccc;
  padding: 50px 30px 20px 30px;
}

input {
  border-radius: 0px;
}

.title {
  font-family: 'apercu-medium';
}
</style>
