<template>
  <div>
    <div class="login-form">
      <form @submit="handleSubmit($event)">
        <h1 class="text-center mb-4">Watch Portal</h1>
        <hr>
        <h3 class="text-center mb-3">Login</h3>
        <div class="form-group">
          <input
            v-model="model.username"
            type="text"
            class="form-control"
            placeholder="Username"
            required
          />
        </div>
        <div class="form-group">
          <input
            v-model="model.password"
            type="password"
            class="form-control"
            placeholder="Password"
            required
          />
        </div>
        <AlertComponent v-if="showAlert" v-bind="alertInfo"></AlertComponent>
        <div class="form-group mt-4">
          <button type="submit" class="btn btn-outline-primary btn-block">Log in</button>
        </div>
      </form>
      <p class="text-center" hidden>
        <router-link to="/register">Register</router-link>
      </p>
    </div>
  </div>
</template>

<script>
import AuthService from '../../core/auth.service'
import AlertComponent from '../alert/alert.component'

export default {
  path: '/login',
  name: 'LoginComponent',
  components: {
    AlertComponent,
  },
  data() {
    return {
      showAlert: null,
      alertInfo: null,
      model: {
        username: null,
        password: null,
      },
    }
  },
  methods: {
    handleSubmit(event) {
      event.preventDefault()
      this.showAlert = false
      if (this.model.username === null || this.model.password === null) return
      let vm = this
      AuthService.login(this.model)
        .then(response => {
          AuthService.setUserIdentity(response.data)
          vm.$emit('EventLoggedIn')
        })
        .catch(function(error) {
          vm.showAlert = true
          vm.alertInfo = { success: false, error }
        })
    },
  },
}
</script>

<style scoped>
.login-form {
  width: 340px;
  margin: 180px auto;
}
.login-form form {
  margin-bottom: 15px;
  background: white;
  box-shadow: 0px 4px 4px #989b9c;
  padding: 30px;
}
.login-form h2 {
  margin: 0 0 15px;
}
.form-control,
.btn {
  min-height: 38px;
  border-radius: 2px;
}
.btn {
  font-size: 15px;
  font-weight: bold;
}
</style>
